* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: "";
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  zoom: 80%;
}
ol,
ul {
  list-style: none;
}
a {
  color: #000;
  text-decoration: none;
}
button {
  background: none;
}
html,
body {
  height: 100%;
  width: 100%;
  min-width: 1200px;
  font-family: "Bebas Neue Regular";
}
@font-face {
  font-family: "Bebas Neue Regular";
  src: url("../fonts/BebasNeueRegular.otf");
}
h3 {
  color: #364356;
  font-family: "Bebas Neue Regular";
  font-size: 100px;
  font-weight: bold;
  text-align: center;
  padding-top: 50px;
}
.header {
  height: 70%;
  background: url("../img/header.png") center;
  background-size: cover;
  position: relative;
  margin-bottom: -150px;
}
.wrapper {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo {
  width: 200px;
  height: 220px;
  background: url("data:image/png;base64,");
  background-size: cover;
  position: absolute;
  right: 10px;
  top: 10px;
}
.losung {
  float: left;
  margin: 70px;
}
.losung > * {
  margin-top: 20px;
}
.losung p {
  display: inline-block;
  background: rgba(199,129,180,0.85);
  color: #f0f8ff;
  font-size: 75px;
  padding: 5px 10px;
  padding-top: 10px;
}
.header__down-block {
  background: url("../img/pink.png") no-repeat;
  background-size: cover;
  height: 460px;
  width: 100%;
  margin-bottom: -4px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.wrapper__dress {
  display: flex;
  align-items: center;
  width: 450px;
}
.wrapper__dress img {
  width: 200px;
  height: 350px;
}
.wrapper__dress .text__dress {
  min-width: 400px;
  height: 200px;
  text-align: left;
  margin-top: 20px;
}
.wrapper__dress .text__dress .consult {
  font-family: "Bebas Neue Regular";
  font-size: 30px;
  color: #f0f8ff;
}
.wrapper__dress .text__dress .recall {
  display: block;
  font-family: "Backto Black Demo";
  font-size: 22px;
  margin-top: 30px;
  font-weight: 400;
  font-style: italic;
  color: #f0f8ff;
}
.call-me {
  display: flex;
  flex-direction: column;
  width: 310px;
}
.call-me input {
  height: 40px;
  width: 285px;
  margin-bottom: 20px;
  border-radius: 10px;
  padding-left: 15px;
  outline: none;
}
.call-me input::placeholder {
  color: #f2726f;
  transition: 0.5s;
  font-size: 18px;
}
.call-me input:focus::placeholder {
  color: transparent;
}
.get_consult {
  height: 45px;
  width: 302px;
  background: #364356;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f0f8ff;
  font-size: 22px;
  font-weight: 100;
  cursor: pointer;
  outline: none;
  background-image: linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.2));
  box-shadow: 0px 2px 0px rgba(0,0,0,0.8);
}
.get_consult:active {
  box-shadow: inset 0px 2px 28px rgba(0,0,0,0.8);
  background-image: linear-gradient(bottom, rgba(0,0,0,0), rgba(0,0,0,0.4));
}
.facts {
  height: 450px;
  background: #364356;
  display: flex;
  align-items: center;
  justify-content: space-around;
  clear: both;
}
.facts .fact {
  width: 320px;
  height: 320px;
  background: url("data:image/png;base64,");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 60px;
}
.facts .fact span {
  color: #fff;
  font-family: "Bebas Neue Regular";
  font-weight: bold;
  font-size: 38px;
}
.facts .fact span:first-child {
  font-size: 60px;
}
.facts .fact span:last-child {
  font-size: 30px;
}
.facts .fact:first-child {
  margin: 0;
}
.facts .fact span {
  font-size: 27px;
}
.pictures {
  display: flex;
  clear: both;
}
.pictures .pic {
  width: 25%;
  height: 100%;
}
.how_we_can_do_it {
  min-height: 100%;
  padding-bottom: 30px;
  position: relative;
}
.how_we_can_do_it h3 {
  font-size: 120px;
  width: 600px;
  margin: 20px auto;
}
.how_we_can_do_it .how_we_can_do_it__left {
  position: absolute;
  left: 0;
  width: 200px;
  height: 90%;
  background: url("data:image/png;base64,");
}
.how_we_can_do_it .how_we_can_do_it__right {
  position: absolute;
  right: 0;
  width: 200px;
  height: 90%;
  background: url("data:image/png;base64,");
}
.how_we_can_do_it .how_we_can_do_it__bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
  background: url("data:image/png;base64,");
}
.how_we_can_do_it__main {
  width: 70%;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  margin-top: 80px;
}
.how_we_can_do_it__main .for_who {
  height: 70px;
  display: flex;
  margin-left: 100px;
  margin-bottom: 40px;
}
.how_we_can_do_it__main .for_who h5 {
  width: 70px;
  font-size: 25px;
  margin: 0 25px;
}
.how_we_can_do_it__main .for_who span {
  font-size: 16px;
  font-family: "Open sans";
  font-style: italic;
}
.how_we_can_do_it__main .for_who .left {
  flex: 1;
  display: flex;
  align-items: center;
}
.how_we_can_do_it__main .for_who .left img {
  width: 39px;
  height: 54px;
}
.how_we_can_do_it__main .for_who .right {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: 50px;
}
.how_we_can_do_it__main .for_who .right img {
  width: 55px;
  height: 54px;
}
.how_we_can_do_it__main .for_who .right span {
  margin-left: 20px;
}
.adverticements {
  display: flex;
  flex-wrap: wrap;
}
.item {
  width: 30%;
  height: 300px;
  margin: 10px;
  text-align: center;
}
.item .heart {
  height: 60px;
  width: 70px;
  background: url("data:image/png;base64,") no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}
.item .heart span {
  margin-top: 10px;
  margin-left: 3px;
  font-size: 20px;
}
.pic {
  width: 100;
  height: 130;
}
.text {
  margin: 15px auto;
  font-size-responsive: 13px 30px 480px 1280px;
}
.select_a_course {
  background: #fff;
  position: relative;
}
.select_a_course .wrapper_containers {
  display: flex;
}
.select_a_course .left_container {
  width: 100%;
}
.select_a_course .left_container .wrapper {
  display: flex;
  max-width: 1200px;
}
.select_a_course .left_container .wrap {
  display: flex;
}
.select_a_course .left_container .hearts {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 830px;
  margin-right: 100;
}
.select_a_course .left_container .love {
  background: url("../img/love_full.png");
  background-size: cover;
  size: 23vw;
  min-width: 350px;
  min-height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}
.select_a_course .left_container .love span {
  color: #364356;
  font-weight: bold;
  font-size: 40px;
  margin-left: 10px;
  margin-bottom: 45px;
  line-height: 0.1;
}
.select_a_course .right_container img {
  position: relative;
  top: 120px;
  width: 500px;
}
.select_a_course__presents {
  width: 75%;
  min-width: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size-responsive: 20px 28px 480px 1280px;
}
.select_a_course__presents .wrapper {
  margin-left: -70px;
}
.present {
  background: url("../img/presents.png");
  min-width: 400px;
  height: 300px;
  background-size: cover;
  margin-left: 70px;
}
.text_dox {
  flex: 1;
  min-width: 700px;
  margin-top: 50px;
}
.text_dox span {
  display: block;
  text-align: center;
  color: #f0f8ff;
  font-size: 32px;
  margin-bottom: 5px;
}
.text_dox__button {
  margin: auto;
  width: 450px;
  font-size: 27px;
}
.what_are_you_get {
  background: url("../img/bg-2.png");
}
.what_are_you_get h3 {
  margin-bottom: 50px;
}
.articles__wrapper {
  max-width: 1600px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.articles__wrapper article {
  width: 21%;
  min-width: 200px;
  min-height: 700px;
  display: flex;
  flex-direction: column;
  margin: 5px;
  text-align: center;
  background: #fff;
  box-shadow: 0px 0px 6px 0px rgba(50,50,50,0.75);
}
.articles__wrapper article img {
  width: 100%;
}
.articles__wrapper article .line {
  height: 30px;
  margin-top: 30px;
  margin-bottom: 20px;
  background: url("data:image/png;base64,") no-repeat center center;
}
.articles__wrapper article h5 {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: 800;
  line-height: 1.2;
  color: #364356;
  padding: 0 10px;
}
.articles__wrapper article span {
  font-size: 20px;
  padding: 20px;
  display: block;
  text-align: justify;
  font-family: "Open sans";
}
.who_write_about_us {
  width: 100%;
}
.who_write_about_us .left-side {
  width: 100%;
  float: left;
}
.faim .wrap {
  display: flex;
  align-items: center;
}
.faim .wrap>* {
  margin-left: 60px;
}
.faim img {
  width: 220px;
}
.faim .r-360 {
  margin-left: 60px;
}
.woman_pic {
  float: right;
  margin-bottom: -100px;
}
.woman_pic img {
  width: 500px;
  position: relative;
  top: 60px;
  z-index: -3;
}
footer {
  height: 190px;
  background: #2d3848;
  text-align: right;
}
.logo-bottom {
  width: 130px;
  height: 140px;
  background: url("data:image/png;base64,");
  background-size: cover;
  float: right;
  margin: 20px;
}
.social {
  display: flex;
  float: left;
  margin-top: 80px;
}
.social .soc {
  display: block;
  margin-left: 40px;
}
.social img {
  size: 50px;
}
.overlay {
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0,0,0,0.7);
  display: none;
}
.popup {
  width: 1000px;
  min-height: 780px;
  background: url("../img/popap-bg.png") no-repeat;
  background-size: cover;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  z-index: 1001;
  margin: auto;
  top: 0;
  right: 0;
  left: 0;
  display: none;
}
.popup .heart {
  background: url("../img/white.png");
  background-size: cover;
  height: 60px;
  width: 70px;
}
.popup .heart span {
  font-size: 30px;
  margin-top: -1px;
  margin-left: -0.5px;
}
.popup .text {
  color: #f0f8ff;
}
.popup h4 {
  display: block;
  background: #2c4156;
  width: 100%;
  margin-bottom: -12px;
  margin-top: 10px;
  height: 50px;
  line-height: 1.5;
  color: #f0f8ff;
  font-size: 30px;
  text-align: center;
}
.discription_popap {
  font-size: 27px;
  width: 500px;
  font-style: italic;
  color: #f0f8ff;
  display: block;
  margin: auto;
  text-align: center;
  margin-top: -30px;
}
.button_popap {
  height: 55px;
  min-width: 250px;
  background: #364356;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f0f8ff;
  font-size: 25px;
  font-weight: 100;
  outline: none;
  margin: 50px auto;
  padding: 0 10px;
}
.line_popap {
  height: 350px;
  background: #2c4156;
  display: flex;
  margin-bottom: 20px;
}
form {
  margin-bottom: 20px;
  margin-left: 350px;
}
